.object {
  width: 8px;
  height: 50px;
  margin-right: 5px;
  background-color: #FFF;
  -webkit-animation: animate 1s infinite;
  animation: animate 1s infinite;
  float: left;
}

.object:last-child {
  margin-right: 0;
}

.object:nth-child(10) {
  -webkit-animation-delay: .9s;
  animation-delay: .9s;
}

.object:nth-child(9) {
  -webkit-animation-delay: .8s;
  animation-delay: .8s;
}

.object:nth-child(8) {
  -webkit-animation-delay: .7s;
  animation-delay: .7s;
}

.object:nth-child(7) {
  -webkit-animation-delay: .6s;
  animation-delay: .6s;
}

.object:nth-child(6) {
  -webkit-animation-delay: .5s;
  animation-delay: .5s;
}

.object:nth-child(5) {
  -webkit-animation-delay: .4s;
  animation-delay: .4s;
}

.object:nth-child(4) {
  -webkit-animation-delay: .3s;
  animation-delay: .3s;
}

.object:nth-child(3) {
  -webkit-animation-delay: .2s;
  animation-delay: .2s;
}

.object:nth-child(2) {
  -webkit-animation-delay: .1s;
  animation-delay: .1s;
}

@-webkit-keyframes animate {
  50% {
    -ms-transform: scaleY(0);
    -webkit-transform: scaleY(0);
    transform: scaleY(0);
  }
}

@keyframes animate {
  50% {
    -ms-transform: scaleY(0);
    -webkit-transform: scaleY(0);
    transform: scaleY(0);
  }
}

