  border-top: 1px solid;
}

.site-branding .site-title a {
  color: #555;
}

.site-footer,
.site-info,
.main-navigation .menu .menu-mobile li,
#wp-calendar,
#wp-calendar tbody td {
  border-color: #e2e2e2;
}

#scroll-top {
  /*     background: #000; */
  background: rgba(0, 0, 0, 0.3);
  color: #fff;
  cursor: pointer;
  position: fixed;
  bottom: 40px;
  right: 30px;
  left: inherit;
  z-index: 999;
}

#scroll-top > i {
  display: block;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 26px;
}

@media screen and (max-width: 991px) {
