/*Support for Gutenberg*/
.wp-block-gallery {
  list-style: none;
  padding: 0;
  margin: 0 auto;
}

.wp-block-quote {
  margin: 30px 0 30px 0 !important;
  padding: 50px !important;
  font-style: normal;
  text-align: left;
  quotes: none;
  box-sizing: border-box;
  font-weight: 400;
  position: relative;
  border-left: 0 !important;
  background: #e8e8e8;
}

.wp-block-quote:before {
  content: "";
  background-image: url(assets/icons/open_quote.svg);
  width: 70px;
  height: 70px;
  position: absolute;
  top: 0;
  left: 40px;
  padding: 0;
  background-color: transparent;
  margin: 0 auto;
}

.wp-block-quote:before,
.wp-block-quote:after {
  background-size: 100%;
}

.wp-block-quote:after {
  content: none;
  display: none;
}

.wp-block-quote cite {
  display: block;
  font-size: 14px;
  font-style: normal;
}

.wp-block-quote.is-style-large {
  margin-bottom: 23px;
  margin-top: 0 !important;
}

.wp-block-quote.is-style-large p {
  font-size: 24px !important;
  line-height: 1.5 !important;
}

.wp-block-quote p {
  font-style: italic;
  font-size: 16px;
  line-height: 25px !important;
}

@media only screen and (min-width: 768px) {
  .alignfull {
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
    width: auto;
    max-width: 1000%;
  }
  .alignwide {
    margin-left: calc(25% - 25vw);
    margin-right: calc(25% - 25vw);
    width: auto;
    max-width: 1000%;
  }
  .alignwide img,
  .alignfull img {
    display: block;
    margin: 0 auto;
  }
}

